

.article-container {
    display: flex;
    flex-flow: column;
  align-content: center;
    width: 100%;
 
    margin: 0 auto;

    padding: 4rem 0 2rem 0;
    
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,83,83,1) 9%, rgba(0,128,128,1) 50%, rgba(0,83,83,1) 89%, rgba(0,0,0,1) 100%);
    min-height: 80vh;
    color: #fff;
  }


.article-container    h1 {
  text-align: center;
  font-size: 34px;
  margin-top: 20px;
color: #fff;
}
  .article{
    background: rgba(0,0,0,0.6); 

    border-radius: 50px;
    box-shadow: 0 5px 15px rgba(255, 255, 255, 0.4);
    transition:  all 0.3s linear;
    align-items: center;
    display: flex;
    justify-content: center;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    height: 60vh;
    text-align: center;
  }
  .article h2{
    font-size: 40px;
       color: #fff;
   }


  .article form{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.article form > h2{
 font-size: 40px;
    color: #fff;
}

.article p{
  font-size: 25px;
}

.article input{
    width: 80%;

    height: 30px;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.article textarea{
    width: 550px;

    height: 100px;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.article select{
  display: flex;

  font-size: 20px;
}
.btn {
    background-color: #008080;
    color: #fff;
    border: 1px solid #008080;
 margin-top: 15px;
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: 5px #000;
    cursor: pointer;
    padding: 12px 26px;
    font-size: 20px;
}

.btn:hover{
    background: #fff;
    color: #242424;
    border: 1px solid #008080;
    
    transition: all 0.3s ease-out;
    
    }

    @media screen and (max-width: 960px) {
.article{
  width: 80%;
}

      .article h2{
  
           font-size: 30px;
           
       }
  
      .article p{
         margin-left: 20px;
         margin-right: 20px;
          font-size: 18px;
          
      }
      .article input{
     
    

        font-size: 16px;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }
    
    .article textarea{

        font-size: 16px;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }
  }